"use client"

import { useState } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { Plus, Truck, Phone, Users, Edit, Eye, EyeOff, RefreshCw, X } from "lucide-react"
import { formatPhone, generatePassword } from "@/lib/utils"
import toast from "react-hot-toast"

export default function DeliveryPersonsPage() {
  const queryClient = useQueryClient()
  const [dialogOpen, setDialogOpen] = useState(false)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [showPassword, setShowPassword] = useState(false)
  
  const [form, setForm] = useState({
    name: "",
    phone: "",
    zone: "",
    password: generatePassword(8),
  })

  const { data, isLoading } = useQuery({
    queryKey: ["delivery-persons"],
    queryFn: async () => {
      const res = await fetch("/api/admin/delivery-persons")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const createMutation = useMutation({
    mutationFn: async () => {
      const res = await fetch("/api/admin/delivery-persons", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Delivery person created")
      queryClient.invalidateQueries({ queryKey: ["delivery-persons"] })
      setDialogOpen(false)
      
      // Show credentials
      toast.custom((t) => (
        <div className="bg-white p-4 rounded-xl shadow-lg border border-gray-200 max-w-sm">
          <p className="font-semibold mb-2 text-gray-900">Credentials Created</p>
          <p className="text-sm text-gray-600">Phone: {form.phone}</p>
          <p className="text-sm text-gray-600">Password: {form.password}</p>
          <p className="text-xs text-amber-600 mt-2">Save these credentials - password won't be shown again!</p>
          <button 
            className="mt-2 px-3 py-1.5 text-sm text-gray-600 hover:bg-gray-100 rounded-lg"
            onClick={() => toast.dismiss(t.id)}
          >
            Dismiss
          </button>
        </div>
      ), { duration: 15000 })
      
      resetForm()
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const updateMutation = useMutation({
    mutationFn: async (data: { id: string; updates: any }) => {
      const res = await fetch(`/api/admin/delivery-persons/${data.id}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data.updates),
      })
      if (!res.ok) {
        const result = await res.json()
        throw new Error(result.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Updated successfully")
      queryClient.invalidateQueries({ queryKey: ["delivery-persons"] })
      setEditingId(null)
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const resetForm = () => {
    setForm({
      name: "",
      phone: "",
      zone: "",
      password: generatePassword(8),
    })
  }

  const deliveryPersons = data?.data?.deliveryPersons || []

  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        
        {/* Page Header */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Delivery Team</h1>
            <p className="text-gray-500 mt-1">Manage delivery person accounts and assignments</p>
          </div>
          <button
            onClick={() => { resetForm(); setDialogOpen(true); }}
            className="inline-flex items-center gap-2 px-5 py-2.5 bg-slate-900 text-white font-medium rounded-xl hover:bg-slate-800 transition-colors"
          >
            <Plus className="w-5 h-5" />
            Add Person
          </button>
        </div>

        {/* Create Dialog */}
        {dialogOpen && (
          <div className="fixed inset-0 z-50 flex items-center justify-center">
            <div className="absolute inset-0 bg-black/50" onClick={() => setDialogOpen(false)} />
            <div className="relative bg-white rounded-2xl shadow-xl w-full max-w-md mx-4 p-6">
              <div className="flex items-center justify-between mb-6">
                <div>
                  <h2 className="text-xl font-bold text-gray-900">Add Delivery Person</h2>
                  <p className="text-sm text-gray-500 mt-1">Share credentials after creation</p>
                </div>
                <button
                  onClick={() => setDialogOpen(false)}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-gray-500" />
                </button>
              </div>
              
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Full Name</label>
                  <input
                    type="text"
                    value={form.name}
                    onChange={(e) => setForm({ ...form, name: e.target.value })}
                    placeholder="Enter full name"
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Phone Number</label>
                  <input
                    type="text"
                    value={form.phone}
                    onChange={(e) => setForm({ ...form, phone: e.target.value })}
                    placeholder="10-digit phone number"
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  />
                  <p className="text-xs text-gray-500 mt-1">This will be used as their login username</p>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Zone/Area</label>
                  <input
                    type="text"
                    value={form.zone}
                    onChange={(e) => setForm({ ...form, zone: e.target.value })}
                    placeholder="e.g., Pondicherry Central"
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Password</label>
                  <div className="flex gap-2">
                    <div className="relative flex-1">
                      <input
                        type={showPassword ? "text" : "password"}
                        value={form.password}
                        onChange={(e) => setForm({ ...form, password: e.target.value })}
                        className="w-full h-11 px-4 pr-10 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                      />
                      <button
                        type="button"
                        onClick={() => setShowPassword(!showPassword)}
                        className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
                      >
                        {showPassword ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                      </button>
                    </div>
                    <button
                      type="button"
                      onClick={() => setForm({ ...form, password: generatePassword(8) })}
                      className="w-11 h-11 flex items-center justify-center border border-gray-200 rounded-xl hover:bg-gray-50 transition-colors"
                      title="Generate new password"
                    >
                      <RefreshCw className="w-4 h-4 text-gray-600" />
                    </button>
                  </div>
                </div>
              </div>

              <div className="flex gap-3 mt-6">
                <button
                  onClick={() => setDialogOpen(false)}
                  className="flex-1 h-11 px-4 border border-gray-200 rounded-xl font-medium text-gray-700 hover:bg-gray-50 transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={() => createMutation.mutate()}
                  disabled={!form.name || !form.phone || !form.password || createMutation.isPending}
                  className="flex-1 h-11 px-4 bg-slate-900 text-white rounded-xl font-medium hover:bg-slate-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  {createMutation.isPending ? "Creating..." : "Create"}
                </button>
              </div>
            </div>
          </div>
        )}

        {/* List */}
        {isLoading ? (
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[1, 2, 3].map(i => (
              <div key={i} className="h-56 bg-white rounded-2xl animate-pulse" />
            ))}
          </div>
        ) : deliveryPersons.length === 0 ? (
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm p-12">
            <div className="text-center">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Truck className="w-8 h-8 text-gray-400" />
              </div>
              <h3 className="text-lg font-semibold text-gray-900 mb-1">No delivery persons yet</h3>
              <p className="text-gray-500 mb-6">Add your first delivery person to get started</p>
              <button
                onClick={() => { resetForm(); setDialogOpen(true); }}
                className="inline-flex items-center gap-2 px-5 py-2.5 bg-slate-900 text-white font-medium rounded-xl hover:bg-slate-800 transition-colors"
              >
                <Plus className="w-4 h-4" />
                Add First Person
              </button>
            </div>
          </div>
        ) : (
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {deliveryPersons.map((person: any) => (
              <div key={person.id} className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
                <div className="p-6">
                  <div className="flex items-start justify-between mb-5">
                    <div className="flex items-center gap-4">
                      <div className="w-14 h-14 bg-emerald-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                        <Truck className="w-7 h-7 text-emerald-600" />
                      </div>
                      <div>
                        <p className="font-semibold text-gray-900 text-lg">{person.name}</p>
                        <p className="text-sm text-gray-500">
                          {person.zone || "No zone assigned"}
                        </p>
                      </div>
                    </div>
                    <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-semibold ${
                      person.isActive 
                        ? "bg-emerald-100 text-emerald-800"
                        : "bg-red-100 text-red-800"
                    }`}>
                      {person.isActive ? "Active" : "Inactive"}
                    </span>
                  </div>

                  <div className="space-y-3 mb-5">
                    <div className="flex items-center gap-3 text-sm text-gray-600">
                      <Phone className="w-4 h-4 text-gray-400" />
                      <span>{formatPhone(person.phone)}</span>
                    </div>
                    <div className="flex items-center gap-3 text-sm text-gray-600">
                      <Users className="w-4 h-4 text-gray-400" />
                      <span>{person._count?.customers || 0} customers assigned</span>
                    </div>
                  </div>

                  <div className="flex gap-3">
                    <button
                      onClick={() => {
                        updateMutation.mutate({
                          id: person.id,
                          updates: { isActive: !person.isActive }
                        })
                      }}
                      className="flex-1 h-10 px-4 border border-gray-200 rounded-xl text-sm font-medium text-gray-700 hover:bg-gray-50 transition-colors"
                    >
                      {person.isActive ? "Deactivate" : "Activate"}
                    </button>
                    <button
                      onClick={() => setEditingId(editingId === person.id ? null : person.id)}
                      className="w-10 h-10 flex items-center justify-center border border-gray-200 rounded-xl hover:bg-gray-50 transition-colors"
                    >
                      <Edit className="w-4 h-4 text-gray-600" />
                    </button>
                  </div>

                  {/* Edit Form */}
                  {editingId === person.id && (
                    <div className="mt-5 pt-5 border-t border-gray-200 space-y-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1.5">Name</label>
                        <input
                          id={`name-${person.id}`}
                          type="text"
                          defaultValue={person.name}
                          className="w-full h-10 px-3 border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1.5">Zone</label>
                        <input
                          id={`zone-${person.id}`}
                          type="text"
                          defaultValue={person.zone}
                          className="w-full h-10 px-3 border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1.5">New Password (optional)</label>
                        <input
                          id={`password-${person.id}`}
                          type="password"
                          placeholder="Leave blank to keep current"
                          className="w-full h-10 px-3 border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                        />
                      </div>
                      <div className="flex gap-3">
                        <button
                          onClick={() => {
                            const name = (document.getElementById(`name-${person.id}`) as HTMLInputElement).value
                            const zone = (document.getElementById(`zone-${person.id}`) as HTMLInputElement).value
                            const password = (document.getElementById(`password-${person.id}`) as HTMLInputElement).value
                            
                            updateMutation.mutate({
                              id: person.id,
                              updates: {
                                name,
                                zone,
                                ...(password && { password })
                              }
                            })
                          }}
                          disabled={updateMutation.isPending}
                          className="flex-1 h-10 px-4 bg-slate-900 text-white rounded-lg text-sm font-medium hover:bg-slate-800 disabled:opacity-50 transition-colors"
                        >
                          {updateMutation.isPending ? "Saving..." : "Save"}
                        </button>
                        <button
                          onClick={() => setEditingId(null)}
                          className="h-10 px-4 text-sm text-gray-600 hover:bg-gray-100 rounded-lg transition-colors"
                        >
                          Cancel
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}

      </div>
    </div>
  )
}
